
<script>
   import lss from 'localstorage-slim';

   import { navigate } from 'svelte-navigator';
   import { user, userTokenTTL } from '../../stores.js';
   
   $: if ($user != null) {
      navigate('/panel', { replace: true });
   }
   let username, password;
   const login = () => {
      $user = {
         username,
         token: 'somerandomstring',
         access: 'sudo'
      };
      lss.set('user', $user, { ttl: userTokenTTL, encrypt: true });
   }
   document.onkeydown = (e) => {
      if (e.keyCode === 13) login();
   }
</script>

<div class="canvas">
   <div class="form">
      <div class="wrapper">
         <input bind:value={username} type="text" id="username" placeholder="نام کاربری">
         <ion-icon name="person-circle-outline"></ion-icon>
      </div>
      <div class="wrapper">
         <input bind:value={password} type="password" id="password" placeholder="رمز عبور">
         <ion-icon name="keypad-outline"></ion-icon>
      </div>
      <button id="login" on:click={login}>ورود</button>
   </div>
</div>

