<script>
   import lss from 'localstorage-slim';
   import { Link } from "svelte-navigator";
   import { location } from "../../../stores.js";

   export let name, path, icon;

   const logout = () => {
      lss.remove('user');
      window.location = '/login';
   }
</script>

<li class:active={$location == name}>
   {#if path !== 'logout'}
      <Link to="{path}">
         <div>{name}</div>
         <ion-icon name="{icon}" />
      </Link>
   {:else}
      <div on:click={logout}>{name}</div>
      <ion-icon name="{icon}" />
   {/if}
</li>

