<script>
   export let options;
   export let def;
   export let value = def;
</script>

<div class="selectBar">
   {#each options as options}
      <div
         on:click={() => {
            value = options;
         }}
         data-valu={options}
         class:selected={value == options}>{options}</div
      >
   {/each}
</div>

