<script>
   import { onMount } from "svelte";

   export let settings;

   var options = {
      series: settings.series,
      chart: {
         height: 350,
         type: "line",
         width: '100%',
         zoom: {
            enabled: false,
         },
         animations: {
            enabled: true,
            easing: "linear",
            speed: 300,
            animateGradually: {
               enabled: false,
            },
            dynamicAnimation: {
               enabled: true,
               speed: 300,
            },
         },
         parentHeightOffset: 15,
         background: '#eeeeee11'
      },
      colors: ['#CDDC39', '#9CCC65'],
      dataLabels: {
         enabled: false,
      },
      stroke: {
         curve: "smooth",
         width: 2,
      },
      title: {
         text: settings.title,
         align: "center",
      },
      xaxis: {
         categories: Array.from({ length: 24 }, (_, i) =>
            Math.floor(i + 1)
         ),
         title: {
            text: settings.xaxis,
         },
      },
      yaxis: {
         title: {
            text: settings.yaxis,
         },
      },
      legend: {
         position: "bottom",
         horizontalAlign: "right",
         markers: {
            offsetY: 3,
         },
      },
      annotations: {
         position: "front",
      },
      noData: {
         text: "در حال بارگذاری...",
      },
      markers: {
         size: 1,
         colors: "#11111122",
         strokeWidth: 0,
         fillOpacity: 1,
         discrete: [],
         shape: "square",
         radius: 7,
         showNullDataPoints: true,
         hover: {
            size: 2,
            sizeOffset: 3,
         },
      },
   };

   const id = "chart-" + (Math.random() + 1).toString(36).substring(2);

   onMount(async () => {
      var chart = new ApexCharts(document.querySelector("#" + id), options);
      chart.render();
   });
</script>

<div class="chart-wrapper">
   <div {id} />
</div>

<style lang="less">.chart-wrapper {
  width: 70%;
  margin-top: 3rem;
}
</style>
