<script>
   import lss from "localstorage-slim";
   import { Router, Route } from "svelte-navigator";
   import { navigate, useLocation } from "svelte-navigator";
   import { location } from "../../stores.js";
   import { user, userTokenTTL } from "../../stores";

   import Tab from "./components/Tab.svelte";
   import Dashboard from "./routes/~dashboard/index.svelte";
   import Archive from "./routes/~archive/index.svelte";
   import Access from "./routes/~access/index.svelte";
   import Logs from "./routes/~logs/index.svelte";
   
   useLocation().subscribe((l) => {
      if (!lss.get("user")) {
         window.location = "/";
      }
      lss.flush();
      if (lss.get("user")) {
         lss.set("user", lss.get("user"), { ttl: userTokenTTL });
      }
      l.pathname == "/panel" && navigate("/panel/dashboard", { replace: true });
   });
</script>

<div id="_app">
   <div class="pure-g">
      <div class="pure-u-1">
         <nav>
            <div class="pure-u-lg-1-6 pure-u-md-2-24 pure-u-sm-2-24" />
            <div class="pure-u-lg-2-3 pure-u-md-5-6 pure-u-sm-5-6">
               <div class="_logo pure-hidden-sm">Bar Power Forcast</div>
               <ul>
                  <Tab name="داشبورد" path="/panel/dashboard" icon="desktop-outline" />
                  <Tab
                     name="آرشیو پیشبینی ها"
                     path="archive"
                     icon="albums-outline"
                  />
                  {#if $user.access === "sudo"}
                     <Tab name="دسترسی" path="/panel/access" icon="people-outline" />
                  {/if}
                  <Tab name="لاگ ها" path="/panel/logs" icon="reader-outline" />
                  <Tab name="خروج" path="logout" icon="power-outline"/>
               </ul>
            </div>
         </nav>
      </div>
      <div class="pure-u-lg-1-6 pure-u-md-2-24 pure-u-sm-2-24" />
      <div
         class="pure-u-lg-2-3 pure-u-md-5-6 pure-u-lg-2-3 pure-u-sm-5-6 _content"
      >
         <div class="_plate">{$location}</div>
         <Router>
            <Route path="" let:params>
               <Dashboard />
            </Route>
            <Route path="dashboard" let:params>
               <Dashboard />
            </Route>
            <Route path="archive" let:params>
               <Archive />
            </Route>
            {#if $user.access === "sudo"}
               <Route path="access" let:params>
                  <Access />
               </Route>
            {/if}
            <Route path="logs" let:params>
               <Logs />
            </Route>
         </Router>
      </div>
   </div>
</div>

