  padding: 3rem;
  box-shadow: 0px 0px 20px -15px #2121217a;
  font-size: 1.5rem;
  border-radius: 3rem 0 3rem 3rem;
}
._content ._plate {
  position: absolute;
  font-size: 1.5rem;
